@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: 'Noto Sans', 'Noto Sans KR', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(132deg, rgba(181,171,221,1) 0%, rgba(158,201,236,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Noto Sans', 'Noto Sans KR', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}
.main{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;  
}

.content{
  position: relative;
  width: 800px;
  height: 500px;
  background-color: #2e2f30;
  border-radius: 8px; 
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.content > div {
  margin: 15px 0;
}

.btn-group{
  display :flex; 
}

.btn{
  background-color: #ff5f5f;
  color: #fff;
  border: none;
  border-radius: 8px;
  width: 140px;
  height: 60px;
  font-size: 22px;
  font-weight: 600;
  margin: 0 10px;
}

.btn:hover{
  background-color: #f34040;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

h1 {
  font-size:60px;
  font-weight: 400;
}

h1 > span {
  color: #ff5f5f;
}

h2 {
  font-size: 75px;
  text-align: center;
}

h2 > span {
  font-size: 30px;
  font-weight: 400;
}

.address {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #eaeaea;
  color: #000;
  border-radius: 18px;
  width: 100px;
  height: 35px;
  position: absolute;
  top: 0px;
  right: 15px;
  font-size: 14px;
  font-weight: 600;
}

.address-connect{
  cursor: pointer;
}

